.feature-media{
	background: #f7f7f7;
	.feature-media-content{
		.data-link{
			cursor: pointer;
			&.hover-effect{
				&:hover{
					background-color: rgba(255,255,255,0.25);
				}
			}
			&:hover{
				.desc{
					&:after{
						content: '';
						display: block;
						border-bottom: solid 3px #00aab9;
						transform: scaleX(0);
						transition: transform 0.75s ease-in-out;
						transform-origin: 100% 50% ;
					}
					&:hover{
						&:after{
							transform: scaleX(1);
							transform-origin: 0 50% ;
						}
					} 
				}
			}
			.feature-media-items{
				.media-date{
					@media screen and (min-width: 768px){
						font-size: 0.8rem;
						padding-bottom: .5rem;
					}
				}
				background-color: #fff;
				min-height: 460px;
				&:hover{
					animation-duration: .25s;
					box-shadow: 0 14px 28px rgba(0,0,0,.25), 
								0 10px 10px rgba(0,0,0,.22);
				}
			}
		}	
	}
}