.hero-banner-section{
	.banner-image{		
	    background-position: center center;
	    background-size:cover;
	    background-repeat: no-repeat;
		    h1{
		    color: #fff;
		    }
	    &.is_image{
			min-height: 300px;
			height: 100%;
	    }
	    &.no-image{
	    	height: 200px;
	    }
	}
	
}

$mediaLargeMinWidth:1024px;
$mediaMediumMinWidth:768px;
$mediaSmallMinWidth:640px;

@media only screen 
and (min-device-width : 320px) 
and (max-device-width : $mediaSmallMinWidth) {
  .hero-banner-section{
		.banner-image{
			min-height: 150px;
			height: 100%;
				background-position: center right;
			.inner-wrap{
				display: none;
			}
		}
		.mobile-inner-wrap{
			h1{
				color: #1A2C69;
			}
		}
	}
}

@media only screen 
and (min-device-width : 641px) 
and (max-device-width : $mediaMediumMinWidth) {
	  .hero-banner-section{
		.banner-image{
			min-height: 150px;
			height: 100%;
			width: 100%;
			background-position: center center;
			.inner-wrap{
				display: block;
			}
		}
		.mobile-inner-wrap{
			display: none;
		}
	}

}