body {
    font-family: 'Muli', sans-serif; 
    overflow-x: hidden;  
    h1{
        font-size: 2.25rem;
        font-weight: 800;
        color: #1A2C69;
        padding: 0.5rem 0 1rem 0 ;
    }
    h2{
        font-size: 1.75rem;
        font-weight: 800;
        color: #1A2C69;
        padding: 0.5rem 0 1rem 0 ;
    }
    h3{
        color: #00aab9;
        font-size: 1.5rem;
        font-weight: 800;
        padding: 0.5rem 0 1rem 0 ;
    }
    h4{
        color: #1A2C69;
        font-size: 1.3rem;
        font-weight: 800;
        padding: 0.5rem 0 1rem 0 ;
    }
    h5{
        font-size: 1.25rem;
        padding: 0.5rem 0 1rem 0 ;
    }
    p{
        padding: 0.5rem 0 1rem 0;
        font-size: 1rem;
    }
    iframe{
        min-height: 400px;
        width: 100%;
    }
    .wp-block-buttons{
        text-align: center;
    }
    .wp-embed-aspect-16-9{
        .wp-block-embed__wrapper{
            position: relative;
            overflow: hidden;
            width: 100%;
            padding-top: 56.25%;
            margin-bottom: 0;
            iframe{
                min-height: 0;
                position: absolute;
                top: 0;
                left: 0;
                bottom: 0;
                right: 0;
                width: 100%;
                height: 100%;
            }
        }
    }
    .large-icons{
        font-size: 4.375rem ;
    }
    .data-link{
        cursor: pointer;
        &.hover-effect{
            &:hover{
                background-color: rgba(255,255,255,0.25);
            }
        }
        &:hover{
            .desc{
                &:after{
                    content: '';
                    display: block;
                    border-bottom: solid 3px #00aab9;
                    transform: scaleX(0);
                    transition: transform 0.75s ease-in-out;
                    transform-origin: 100% 50% ;
                }
                &:hover{
                    &:after{
                        transform: scaleX(1);
                        transform-origin: 0 50% ;
                    }
                } 
            }
        }
    }
    #page{
        #logo {
            img{
                max-width: none;
                height: 50px;
            }
        }
        nav{
            .header-top-navs{
                li{
                    &.members-login{
                        background-color:#00aab9; 
                        &:hover{
                            background-color: #3BACEE;
                        }
                    }
                    &#search-btn{
                        display: none;
                    }
                    &.search-icon{
						padding: 15px;
					}
                }
            }
            ul#mega-nav{
                .mega-menu-wrap{
                    .mega-menu-megamenu{
                        &.mega-sub-menu{
                            &.mega-menu-item{
                                a{
                                    display: inline-block;
                                    position: relative;
                                    text-decoration: none;
                                    &:after{
                                        content: '';
                                        display: block;
                                        /*border-bottom: solid 3px #00aab9;*/
                                        transform: scaleX(0);
                                        transition: transform 0.75s ease-in-out;
                                        transform-origin: 100% 50% ;
                                    }
                                    &:hover{
                                        color: #1a2c69;
                                        font-weight: 800;
                                        &:after{
                                            transform: scaleX(1);
                                            transform-origin: 0 50% ;
                                        }
                                    }
                                    &.mega-menu-link{
                                        line-height: 1.5;
                                    }

                                }
                                ul{
                                    li{
                                        a{
                                            display: inline-block;
                                            position: relative !important;
                                            text-decoration: none !important;
                                            &:hover{
                                                color: #1a2c69 !important;
                                                font-weight: 800 !important;
                                                &:after{
                                                    transform: scaleX(1) !important;
                                                    transform-origin: 0 50% !important;
                                                }
                                            }  
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        header.bg-brand {
            background-color:#1a2c69;
        }
        .search-click{
            display: block !important; 
            transform: scale(1, 1) !important; 
            opacity: 1 !important; 
            top: 115px !important;
            padding: 50px;
            transition: transform 400ms cubic-bezier(0.645, 0.045, 0.355, 1) 0s ;
        }
        #search-outer #close{
            position: absolute;
            top: 25px;
            right: 25px;
        }
        .entry-content{
            background-color: #f7f7f7;
        }

    }
    .section-full-width{
        background-color: #fff;
        padding: 0 1.5rem;
        text-align: center;
        h2{
            color: #00aab9;
            line-height: 1.875rem;
        }
    }
    .four-column-image{
        background-color: #f7f7f7;
        padding: 0 10rem;
        .wp-block-column{
            &:not(:first-child){
                margin-left: 0;
            }
        }
        .four-column-inner{
            background-color: #fff;
            margin-top: 2rem; 
            .wp-block-column{
                background-color: #f7f7f7;
            }
            .single-col-four{
                background-color: #fff;
                margin-bottom: 1.5rem;
                text-align: left;
                position: relative;
                width: 25%;
                padding: 0.5rem;
                margin: 0 .5rem;
            }
        }
    }
    .two-column-image{
        .wp-block-column{
            &:not(:first-child){
                margin-left: 0;
            }
            .wp-block-cover{
                &.has-background-dim{
                    &:not(.has-background-gradient){
                        &:before{
                            opacity: 0;
                        }   
                    }
                } 
                .wp-block-cover__gradient-background{
                    opacity: 0;
                }
            }
        }
    }
    .wp-block-button{
        a{
            transition: color .3s;  
            z-index: 1;
            position: relative;
            overflow: hidden;
            &:after{
                position: absolute;
                top: 90% ;
                left: 0;
                width: 100%;
                height: 100%;
                background: #1a2c69;
                content: "";
                z-index: -2;
                transition: transform .3s;
            }  
            &:hover{
                &:after{
                    transform: translateY(-80%);
                    transition: transform .3s;
                }
            } 
        }          
    }
    .four-col-icon{
        margin-bottom: 4rem;
        padding: 4rem 1.5rem;
        background: #fff;

        .desc{
            font-weight:400;
        }
        h2{
            text-decoration: none;
            padding-bottom: 4rem;
        }
        p{
            padding-bottom: 2.5rem;
        }

    }
    .two-col-icon-content{
        padding: 2.5rem 6rem;
        padding-bottom: 4rem;
        background-color: #f7f7f7;

        .wp-block-social-links{
            .wp-social-link{
                color: #00aab9;
                background: transparent;
                margin-right: 30px;
                svg{
                    width: 40px;
                    height: 40px;
                }
            }
        }
    }
    .two-col-highlight-message-section{
        .column-1{
            background-color: rgba(1,170,185,.1);
        }
        .column-2{
            background-color: rgba(26,44,105,.1);
        }

    }
    .two-col-content-section{
        h2{
            font-size: 1.75rem;
            padding-bottom: 1rem;
            &:after{
                content:'';
                width: 5rem;
                height:0.06rem;
                display: block;
                bottom: 0;
                background-color: #1a2c69;
                left: 0;
                margin-top: 10px;
                font-weight: 600;
            }			
        }

        p{
            margin-bottom: 20px;
        }
        .wp-block-columns{
            padding-bottom: 0;
        }
    }
    .resource-content-section{
        margin-bottom: 2rem;
        h2{
            font-size: 1.75rem;
            padding-bottom: 1rem;
            &:after{
                content:'';
                width: 5rem;
                height:0.06rem;
                display: block;
                bottom: 0;
                background-color: #1a2c69;
                left: 0;
                margin-top: 10px;
                font-weight: 600;
            }			
        }
        .wp-block-media-text{
            .wp-block-media-text__media{
                padding-right: 1rem;
                img{
                    width: auto;
                }
            }
            
            figure{
                padding-top: 0.6rem;
                padding-bottom: 0.6rem;
            }
            .wp-block-media-text__content{
                padding: 0;
                p{
                    padding-bottom: 0;
                }
            }
            &:last-child {
                margin-bottom: 2rem;
            }
        }
        &.no-border{
            .wp-block-group{
                .wp-block-media-text{
                    &:last-of-type{
                        &:after{
                            content: none;
                        }
                    }
                }
                
            }
        }
    }
    &.home {
        .two-column-image-block{
            
            @media (min-width:320px) {
                    padding: 1rem;
                }
                @media (min-width:1025px){
                    padding: 3.25rem 6rem 3.25rem;
                }
        }
        .four-block-icon{
            .wp-block-columns{
                .wp-block-column{
                    display: flex;
                    flex-direction: column;
                }
                .wp-block-buttons{
                    margin: 0 auto;
                }
            }
        }
    }
    .two-column-image-block, .three-column-image-block{
        background-color: #f7f7f7;
        padding: 2.25rem 6rem 0;
        margin-bottom: 0;

        .wp-block-column{
            background-color: #fff;
            padding: 1rem;
            img{
                width: 100%;
            }
            figcaption{
                font-size: 1.6rem;
                color: #454546;
                line-height: 1.75rem;
                text-align: center;
                font-weight: 700;
            }
        }
    }
    .two-column-image-block{
        .wp-block-column{
            img{
                object-fit: cover;
                min-height: 350px;
                height: 100%;
            }
        }
    }

    .three-column-image-block{
        .wp-block-column{
            img{
                object-fit: cover;
                min-height: 220px;
                height: 100%;
            }
        }
    }
    @media (min-device-width:1024px){
        .two-column-cover-block, .four-column-cover-block{
            background-color: #fff;
            padding: 2.25rem 6rem 0;
            margin-bottom: 0;
            .wp-block-column{
                .wp-block-cover{
                    min-height: 200px;
                    .wp-block-cover__inner-container{
                        position: absolute;
                        bottom: 10px;
                        .title{
                            font-size: 1.25rem;
                        }
                        .description{
                            padding: 0;
                        }
                        .wp-block-buttons{
                            margin-top: 15px;
                        }
                    }
                    &.has-background-dim{
                        .wp-block-cover__inner-container{
                            width: 100%;
                        }
                    }
                }
            }
        }
    }
    
    @media (max-device-width: 1024px) {
        .two-column-cover-block, .four-column-cover-block{
            background-color: #fff;
            padding: 2.25rem 1.5rem 0;
            margin-bottom: 0;
            .wp-block-column{
                .wp-block-cover{
                    min-height: 200px;
                    .wp-block-cover__inner-container{
                        .title{
                            font-size: 1.25rem;
                        }
                        .wp-block-buttons{
                            margin-top: 15px;
                        }
                    }
                    &.has-background-dim{
                        .wp-block-cover__inner-container{
                            width: 100%;
                        }
                    }
                }
            }
        }
    }
    @media screen {
        &.page-template-overview {
            .four-col-icon{
                margin-bottom: 0;
                padding: 2rem 6rem 0;
            }
        }
    }

    @media (max-device-width: 1024px) {
        &.page-template-overview {
            .four-col-icon{
                margin-bottom: 0;
                padding: 2rem 1.5rem 0;
            }
        }
    }
    
    .single-content-block{
        margin-bottom: 2rem;
        h2{
            font-size: 1.75rem;
            padding-bottom: 1rem;
            &:after{
                content:'';
                width: 5rem;
                height:0.06rem;
                display: block;
                bottom: 0;
                background-color: #1a2c69;
                left: 0;
                margin-top: 10px;
                font-weight: 600;
            }			
        }
        p{
            line-height: 1.5rem;
            padding-bottom: 1.6rem;
            strong{
                font-weight: 800;
            }
        }
        ul{
            margin-left: 1rem;
            padding-bottom: 1.6rem;
            li{
                padding-left: 0.2rem;
                line-height: 1.5rem;
                text-indent: 0;
                list-style: disc;
                list-style-position: outside;
            }
        }
        .wp-block-table{
            &.is-style-stripes{
                border-bottom: none;
            }
        }
        .video-shortcode{
            .mejs-volume-button{
                a{
                    display: none;
                }
            }
        }
    }
    .three-col-img{
        h2{
            margin-bottom: 1.4rem;
            &:after{
                content:'';
                width: 5rem;
                height:0.06rem;
                display: block;
                bottom: 0;
                background-color: #1a2c69;
                left: 0;
                margin-top: 10px;
                font-weight: 600;
            }			
        }
        h4{
            padding: 0;
        }
        p{
            &.position, &.description{
                padding: 0;
            }
        }
    }
    .wp-block-separator{
        margin-top: 2.5rem;
        height: 1px;
        margin-bottom: 2.5rem;
        background-color: #ddd;
    }
    .two-col-highlight-message-section{
        padding-top: 1rem;
        padding-bottom: 1rem;
        h2{
            font-size: 1.75rem;
            padding-bottom: 1rem;
            &:after{
                content:'';
                width: 5rem;
                height:0.06rem;
                display: block;
                bottom: 0;
                background-color: #1a2c69;
                left: 0;
                margin-top: 10px;
                font-weight: 600;
            }			
        }
    }
    .single-content-highlight-message{
        margin-top: 4rem;
        margin-bottom: 4rem;
        padding: 2rem;

    }
    .half-content-half-image-block{
        padding-top: 1rem;
        padding-bottom: 1rem;
    }

    #footer-widgets{
        background-color: #1a2c69;
        h4{
            margin-bottom: 12px;
            color: #00aab9;
            font-weight: bold;
        }
    }
    #copyright{
        height: 90px;
        color: #454547;
        .copy{
            color: #454547;
        }
    }
    #primary{
        .entry-content{
            .wp-block-columns{
                &:last-child{
                    margin-bottom: 0;
                }
            }            
        }
    }
    .members-login{
        a{
            display:flex;
            padding: 15px 40px 15px;
        }
    }
    .center-con {
        display: flex;
        height: 100vh;
        align-items: center;
        justify-content: center;
    }
    .round {
        position: relative;
        border: none;
        width: 30px;
        height: 30px;
        border-radius: 100%;
    }
    #cta{
        width:100%;
        cursor: pointer; 
        position: absolute;
        .arrow{
            left: 30%;
            position: absolute; 
            bottom: 0;  
            margin-left:0px; 
            width: 12px; 
            height: 12px; 
            background-size: contain; 
            top:8px;
            &.segunda{
                margin-left: 8px;
            }
            &.next {
                background-image: url(data:image/svg+xml;base64,);
            }
        }
    }

    @keyframes bounceAlpha {
        0% {
            opacity: 1; 
            transform: translateX(0px) scale(1);
        }
        25%{
            opacity: 0; 
            transform:translateX(10px) scale(0.9);
        }
        26%{
            opacity: 0; 
            transform:translateX(-10px) scale(0.9);
        }
        55% {
            opacity: 1; 
            transform: translateX(0px) scale(1);
        }
    }
    .round .arrow{
        animation-name: bounceAlpha;
        animation-duration:1.4s;
        animation-iteration-count:infinite;
        animation-timing-function:linear;
        &.primera{
            animation-name: bounceAlpha;
            animation-duration:1.4s;
            animation-delay:0.2s;
            animation-iteration-count:infinite;
            animation-timing-function:linear;
            &.bounceAlpha {
                animation-name: bounceAlpha;
                animation-duration:1.4s;
                animation-delay:0.2s;
                animation-iteration-count:infinite;
                animation-timing-function:linear;
            }
        }
    }
    &.page-template-standard-content{
        .content-block{
            table{
                tr{
                    line-height: 2.5rem;
                    td{
                        a{
                            line-height: 1.5rem;
                        }
                    }
                }
            }
            .gform_wrapper{
                select, textarea, input{
                    border: .5px solid #1A2D69;
                    border-radius: 0;
                }
                input{
                    &.gform_button{
                        border: none;
                        transition: color .3s;
                        z-index: 1;
                        position: relative;
                        overflow: hidden;

                    }
                }
            }
            .wp-block-social-links{
                .wp-social-link{
                    color: #00aab9;
                    background: transparent;
                    padding-right: 60px;
                    a{
                        color:#00aab9;
                    }
                    svg{
                        width: 40px;
                        height: 40px;
                    }
                }
            }
        }
    }
    .faq-section{
        ol{
            li{
                line-height: 2rem;
                margin-bottom: 20px;
            }
        }
    }

    .parent-sidebar-menu{
        padding: 0 0.5rem 0 0.5rem;
    }
    ul{
        &.child-sidebar-menu{
            >li{
                >a{
                    font-weight:700;
                }
            }
        }
    }
    &.single-media-release{
        .entry-content{
            .single-container{
                p{
                    a{
                        display: inline-block;
                        position: relative;
                        font-weight: 700;
                        color: #1A2C69;
                        text-decoration: none;
                        &:after{
                            content: '';
                            display: block;
                            border-bottom: solid 3px #00aab9;
                            transform: scaleX(0);
                            transition: transform 0.75s ease-in-out;
                            transform-origin: 100% 50% ;
                        }
                        &:hover{
                            &:after{
                                transform: scaleX(1);
                                transform-origin: 0 50% ;
                            }
                        }
                    }
                }
            }
        }
    }
    &.page-template-overview{
        .entry-content{
            .main-container{
                p,li{
                    a{
                        display: inline-block;
                        position: relative;
                        font-weight: 700;
                        color: #1A2C69;
                        text-decoration: none;
                        &:after{
                            content: '';
                            display: block;
                            border-bottom: solid 3px #00aab9;
                            transform: scaleX(0);
                            transition: transform 0.75s ease-in-out;
                            transform-origin: 100% 50% ;
                        }
                        &:hover{
                            &:after{
                                transform: scaleX(1);
                                transform-origin: 0 50% ;
                            }
                        }
                    }
                }
            }
        }
    }
}
$mediaLargeMinWidth:1024px;
$mediaMediumMinWidth:768px;
$mediaSmallMinWidth:640px;





@media only screen 
and (min-device-width : 320px) 
and (max-device-width : $mediaSmallMinWidth) {
    body {
        #page{
            nav{
                .mega-menu-wrap{
                    margin-left: 5px;
                    ul{
                        li{
                            &.mega-menu-megamenu{
                                >.mega-sub-menu{
                                    a{
                                        font-size: 14px !important;
                                    }
                                    li{
                                        &.mega-menu-item{
                                            padding: 5px 15px !important;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        .section-full-width{
            padding: 1rem 1.5rem;
            background-color: #fff;
            text-align: center;
        }
        .four-column-image{
            padding: 1rem;
            .four-column-inner{
                margin: 0;
                .single-col-four{
                    width: 100%;
                }
            }
        }
        .two-column-image-block, .three-column-image-block{
            padding: 1rem;
            .wp-block-column{
                img{
                    object-fit: cover;
                    height: 220px;
                }
            }
        }

        .latest-event-slider{
            padding: 1rem;
        }

        .two-col-icon-content{
            margin: 0;
            padding: 2.5rem 1rem 4rem;
        }
        .four-col-icon{
            padding: 1rem;
            .wp-block-column{
                .wp-block-columns{
                    .wp-block-column{
                        padding-bottom: 2rem;
                        p{
                            height: 0;
                            padding-bottom: 1.5rem;
                        }
                    }
                }
            }
        }
        .single-content-block{
            padding: 0 1.75rem;
        }
        .resource-content-section{
            padding: 0 1.75rem;
            .wp-block-media-text{
                grid-template-columns: unset;
                display: flex;
                flex-wrap: wrap;
            }
        }
        .two-col-content-section{
            padding: 0 1.5rem;
        }
        .three-col-img{
            padding: 0 1.5rem;
            h2{
                text-align: center;
                &::after{
                    width: 100%;
                }
            }
            .wp-block-column{
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                margin-bottom: 2rem;
            }
        }
        #copyright{
            .copy{
                color: #454547;
            }
        }
    }
}

@media only screen 
and (min-device-width : $mediaSmallMinWidth) 
and (max-device-width : $mediaMediumMinWidth) {
    body {
        .four-column-image{
            padding: 4rem;
            .four-column-inner{
                margin: 0;
                flex-direction: column;
                .wp-block-column{
                    flex-basis: unset !important;
                    .single-col-four{
                        width: 100%;
                        margin: 0 2.5rem;
                        img{
                            width: 100%;
                        }
                    } 

                }
            }
        }
        .two-column-image-block, .three-column-image-block{
            flex-direction: column;
            .wp-block-column:nth-child(2n){
                margin-left: 0;
            }
        }
        .section-full-width{
            padding: 1rem 1.5rem;
        }
        .four-col-icon{
            flex-direction: column;
        }
        #copyright{
            .copy{
                color: #454547;
            }
        }
    }
}
@media screen and (max-width: 1195px) {
    body{
        #page{
            nav{
                .header-top-navs{
                    li{
                        &.members-login{
                            min-width: 125px;
                            width: 100%;
                            a{
                                font-size: 0.75rem;
                                padding: 10px 15px 5px;
                            }
                        }
                    }
                }  
            }
        } 
        .round{
            width: 10px;
            height: 10px;
        }
        .arrow{
            top: 3px;
        }
    }
}
@media screen and (min-width: 768px) {
    body{
        .two-column-image-block, .three-column-image-block{
            .wp-block-column{
                transition: all 0.3s cubic-bezier(.25,.8,.25,1);
                &:hover{
                    animation-duration: 0.25s;
                    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
                }
            }
        }
    }
}
@keyframes example {
    0%   {
        border-left: 2px solid #ffffff;
    }
    25%  {
        border-left: 3px solid #1a2c69;
    }
    50%  {
        border-left: 4px solid #1a2c69;
    }
    100% {
        border-left: 5px solid #1a2c69;
    }
}

